@use '../core/variables' as *;
@use '../core/mixins' as *;

.order-place {
	// .order-place__content
	&__content {
		// background-image: var(--radial-gradient);
		position: relative;
		z-index: 9;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 30px;
		padding: 13rem 5rem 16.2rem;

		@include screen-690 {
			border-radius: 15px;
			padding: 50px 10px 60px;
		}
	}

	// .order-place__body
	&__body {
		position: relative;
	}

	// .order-place__body--txt-color-w
	&__body--txt-color-w {
		color: #fff;

		.order-form__column {
			&:not(:last-child) {
				border: 1px solid #fff;
			}
		}

		.checkbox__input:checked + .checkbox__checked .icon-angle-down {
			color: $color;
		}

		.checkbox,
		.button {
			height: 100%;
			background: #fff;
			span {
				display: block;
				color: #000;
				opacity: 1;
			}
		}

		.order-form__button {
			&:hover {
				@include min-960 {
					.icon {
						transform: rotate(-45deg);
					}

					span {
						color: #fff;
					}

					&::before {
						background: $color;
					}
				}
			}
		}
	}

	// .order-place__body--txt-color-b
	&__body--txt-color-b {
		color: #000;
		.order-place__content {
			background: url('../img/main/fon.png') center / cover no-repeat;
		}
		.order-form__column {
			border: 1px solid rgba(0, 0, 0, 0.2);
		}

		.checkbox__input:checked + .checkbox__checked .icon-angle-down {
			color: #fff;
		}

		.checkbox,
		.button {
			background: $color;
			span {
				display: block;
				color: #fff;
				opacity: 1;
			}
		}

		.order-form__button {
			.icon {
				background: #fff;
			}
			.icon-arrow-right {
				&::before {
					color: $color;
					font-weight: 500;
				}
			}
			&:hover {
				@include min-960 {
					.icon {
						transform: rotate(-45deg);
						background: $color;

						.icon-arrow-right {
							&::before {
								color: #fff;
							}
						}
					}

					span {
						color: #000;
					}

					&::before {
						background: #fff;
					}
				}
			}
		}
	}

