@use '../core/variables' as *;
@use '../core/mixins' as *;

.portfolio-performance {
	// .performance__wrapper
	&__wrapper {
		height: calc(100vh - 88px);
		padding-left: 50px;
		padding-right: 50px;

		@include screen-1440 {
			height: 100%;
			padding-block: 50px 60px;
			padding-left: 30px;
			padding-right: 30px;
		}

		@include screen-690 {
			padding-left: 10px;
			padding-right: 10px;
		}
	}

	// .performance__body
	&__body {
		height: 100%;
		width: 100%;
		display: flex;
		align-items: center;

		@include screen-768 {
			padding: 0;
		}
	}

	// .performance__content
	&__content {
		position: relative;
		display: flex;
		align-items: center;
		height: 100%;

		@include screen-768 {
			padding: 0 30px;
		}

		@include screen-375 {
			padding: 0 10px;
		}
	}

	// .performance__column
	&__column {
		height: 100%;
		flex: 0 1 50%;
		display: flex;
		flex-direction: column;
		justify-content: center;

		@include screen-768 {
			flex: 0 1 auto;
		}

		&:nth-child(2) {
			@include screen-768 {
				display: none;
			}
		}
	}

	// .performance__title
	&__title {
		margin-bottom: 43px;

		@include screen-768 {
			margin-bottom: 0;
		}
	}

	// .performance__text
	&__text {
		margin-bottom: 57px;

		@include screen-768 {
			margin-bottom: 40px;
		}
	}

	// .performance__button
	&__button {
		background: $color;
		border: 1px solid transparent;
		color: #fff;

		&:hover {
			border: 1px solid $color;

			.icon {
				transform: rotate(-45deg);
				background: $color;

				.icon-arrow-right {
					&::before {
						color: #fff;
						font-weight: 500;
					}
				}
			}

			span {
				color: #000;
			}

			&::before {
				background: #fff;
			}
		}

		@include screen-768 {
			&.button {
				width: 195px !important;
				align-self: center;

				span {
					font-size: 16px;
				}

				.icon {
					width: 28px;
					height: 28px;

					._icon {
						&::before {
							font-size: 1.8rem;
						}
					}
				}
			}
		}
	}

	// .performance__icon
	&__icon {
		.icon-arrow-right {
			&::before {
				color: $color;
				font-weight: 600;
			}
		}

		&.icon {
			background: #fff;
		}
	}

	// .performance__image

	&__image {
		height: 100%;
		width: 100%;

		@include screen-768 {
			display: flex;
			justify-content: flex-end;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
			object-position: center;

			@include min-768 {
				padding-bottom: 60px;
			}

			@include screen-768 {
				max-width: 400px;
				margin-bottom: 20px;
			}
		}
	}
}
