@mixin _1024 {
	@container (max-width: 1024px) {
		@content;
	}
}

@mixin add-class-on-hover($selector, $class) {
	#{$selector} {
		position: relative;
		overflow: hidden;

		&:hover {
			&.#{$class} {
				// Стили для состояния наведения с классом
				@content;
			}
		}
	}
}
